.close-circle:hover {
    color: white;
    background-color: lightcoral;
    cursor: pointer;
}

@media (max-width: 900px) { 
    .auth-window {
        width: 50%;
    }
}

@media (max-width: 500px) {
    .auth-window {
        width: 70%;
    }

    .auth-bottom-buttons {
        width: 90%;
        height: 40px;
    }

    .auth-button {
        font-size: 12px;
    }

    .vk-reg {
        background-color: #2c3343;
        height: 40px;
        width: 40px;
    }
}