.comissions-coloumn{
    display: flex;
    flex-direction: column;
    background-color: white;
    margin-bottom: 20px;
    border-radius: 15px;
    width: 40%;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
}

.com-title{
    text-align: center;
    margin-top: 7px;
    font-size: 15px;
    font-weight: normal;
    color: #2c3343;
}

.com-main-title{
    text-align: center;
    margin-top: 10px;
    margin-bottom: 0px;
    font-size: 22px;
    color: #2c3343;
}
.comissions-grid{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    transition-duration: 300ms;
}
.com-item{
    /* font-size: 16px; */
    margin: 10px;
    height: 80px;
    width: 80px;
    /* min-height: 5vh; */
    /* min-width: 5vw; */
    transition-duration: 300ms;
    display: flex;
    flex-direction: column;
    font-weight: 600;
    justify-content: center;
    align-items: center;
    padding: 1%;
}

.com-picture{
    background-color: #2c3343;
    background-repeat: no-repeat;
    mask-size: 100% 100%;
    height: 100%;
    width: 100%;
}

.com-picture-pushed{
    background-color: #fae7de;
}

.com-item:hover:not(.com-not-active){
    background-color: #fae7de;
    transition-duration: 300ms;
    border-radius: 10px;
    transform: scale(1.1);
    cursor: pointer;
}

.pm-design .com-picture{
    mask-image: url("../../../media/sprite.svg#PM-Design");
    mask-size: 75% 75%;
    mask-repeat: no-repeat;
    mask-position: center;
}
