}

.acc {
    color: brown;
}

@media (max-width: 900px) {
    .com-title {
        font-size: 14px;
    }
    .com-item {
        width: 80px;
        height: 80px;
    }
}

@media (max-width: 500px) {
    .comissions-coloumn {
        width: 90%;
    }

    .com-main-title {
        font-size: 17px;
    }

    .com-title {
        font-size: 12px;
    }

    .com-item {
        width: 70px;
        height: 70px;
    }
}
