.current-task-item-prev{
    display: flex; 
    justify-content: flex-end;
    height: 20px !important;
    min-height: min-content !important;  
    width: 100%;
}

@media (max-width: 500px) {
    .current-task-item-prev {
        height: 10px !important;
    }
}

.current-task-item{
    display: flex;
    flex-direction: column;
    width: 100%;
    min-height: max-content;
    align-items: center;
    /* font-family: "PFBeauSansPro";
    font-weight: normal; */
}
.current-task-left{
    background-color: #fae7de;
    /* width: 100px; */
    border-top-left-radius: 6.15vh;
    border-bottom-left-radius: 6.15vh;
    margin-right: 0;
    transition-duration: 300ms;
}

.current-task-eye{
    mask-image: url("../../../media/sprite.svg#eye");
    background-color: #2c3343;
    width: 30px;
    height: 30px;
    border: 1px solid black;
    mask-repeat: no-repeat;
    mask-size: 75% 75%;
    mask-position: center;
    margin-left: 5px;
}

.current-task-left.current-task-description-show{
    transform: rotate(90deg) translateY(20px) translateX(20px);
}

.current-task-left.current-task-description-hide:hover{    
    margin-right: 0.5vw;
    transition-duration: 300ms;
}


.current-task-right{
    height: inherit;
    display: flex;
    background-color: #fae7de;
    border-top-right-radius: 6.15vh;
    width: 100%;
    border-bottom-right-radius: 6.15vh;
    width: 35vw;
}
.current-task-name{
    padding-left: 15px;
    width: 78.5%;
    /* height: inherit; */
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 18px !important;
    padding: 10px 0;
}
.current-task-buttons{
    display: flex;
    width: 21.5%;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 10px 0;
}

.current-task-buttons div{
    transition-duration: 200ms;
}

.current-task-buttons div:hover{
    transform: scale(1.25);
    transition-duration: 200ms;
}
.current-task-add{
    margin-right: 10px;
    mask-image: url("../../../media/sprite.svg#plus");
    background-color: #2c3343;
    mask-size: 100% 100%;
    width: 30px;
    height: 30px;
    mask-repeat: no-repeat;
    mask-position: center;
}

.current-task-right{
    transition-duration: 300ms;
}


.current-task-item > .current-task-description-hide{
    height: 0px;
    width: 0px;
    overflow: hidden;
    transition-duration: 300ms;
}


.current-task-item > .current-task-description-show{
    transition-duration: 300ms;
    margin-top: 15px;
    border-radius: 25px;
    height: 10vh;
    width: 100%;
    padding: 20px;
    background-color:#fae7de ;
}

@media (max-width: 500px) {
    .current-task-add {
        width: 17px;
        height: 17px;
    }

    .current-task-name {
        font-size: 13px !important;
    }
}